@use './utils/utils' as u;

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  scroll-behavior: smooth;
  overflow-x: hidden;
  font-family: u.$textFamily;
  color: u.$primaryColor;
  font-size: u.$textSize;
  a {
    color: u.$primaryColor;
    transition: color 0.2s;
    text-decoration: none;
  }
  a:hover {
    color: u.$secondaryColor;
  }
  h1 {
    font-family: u.$headingFamily;
    font-size: u.$headingSize;
  }
}


