.Nav{position:fixed;top:0;left:0;width:100%;padding:0px 20px;z-index:99}.Nav .Nav__icons{display:flex;align-items:center;justify-content:space-between;padding:10px 0}.Nav #Nav__menu{z-index:-1;background-color:#fff;position:fixed;left:0;right:0;top:0;display:flex;justify-content:center;align-items:center;text-align:center;transition:all .5s}.Nav #Nav__menu ul{list-style:none}.Nav #Nav__menu .Nav__menu__main li{font-family:"Montserrat Subrayada",sans-serif;font-size:4em;margin:25px}.Nav #Nav__menu .Nav__menu__footer{position:fixed;bottom:0;left:0;right:0;justify-content:center;display:flex}.Nav #Nav__menu .Nav__menu__footer li{font-family:"Montserrat Subrayada",sans-serif;font-size:2em;margin:25px}.Nav .Nav__menu__open{height:100%;opacity:100%}.Nav .Nav__menu__close{overflow:hidden;height:0;opacity:0%}@media only screen and (min-width: 1000px){.Nav{padding:0 150px}}#logo{height:calc(30px + (65 - 30) * ((100vw - 300px) / (1600 - 300)))}#nav-icon4{width:calc(1.414213 * calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300))));height:calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300)));justify-self:right;transform:rotate(0deg);transition:.5s ease-in-out}#nav-icon4 span{display:block;position:absolute;height:2px;width:100%;background:#2a2a2a;border-radius:3px;opacity:1;left:0;transform:rotate(0deg);transition:.25s ease-in-out}#nav-icon4 span:nth-child(1){top:0px;transform-origin:left center}#nav-icon4 span:nth-child(2){top:calc(calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300))) / 2);transform-origin:left center}#nav-icon4 span:nth-child(3){top:calc(calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300))) / 1);transform-origin:left center}#nav-icon4.open span:nth-child(1){transform:rotate(45deg);top:0px;left:0px}#nav-icon4.open span:nth-child(2){width:0%;opacity:0}#nav-icon4.open span:nth-child(3){transform:rotate(-45deg);top:calc(30px + (40 - 30) * ((100vw - 300px) / (1600 - 300)));right:0px}
.cursor{width:40px;height:40px;border:2px solid #e08c65;border-radius:100%;position:fixed;transform:translate(-50%, -50%);pointer-events:none;transition:all 150ms ease;transition-property:background-color,opacity,transform,mix-blend-mode;z-index:9999;mix-blend-mode:difference}.cursor--hidden{opacity:0}.cursor--link-hovered{transform:translate(-50%, -50%) scale(1.25);background-color:#e08c65}.cursor--clicked{transform:translate(-50%, -50%) scale(0.9);background-color:#e08c65}html *,body *{cursor:none}
*{padding:0;margin:0;box-sizing:border-box}body{scroll-behavior:smooth;overflow-x:hidden;font-family:"Poppins",sans-serif;color:#2a2a2a;font-size:calc(12px + (20 - 12) * ((100vw - 300px) / (1600 - 300)))}body a{color:#2a2a2a;transition:color .2s;text-decoration:none}body a:hover{color:#e08c65}body h1{font-family:"Montserrat Subrayada",sans-serif;font-size:calc(5em + (10 - 8) * ((100vw - 300px) / (1600 - 300)))}
.App .App__title{text-align:center;color:#e08c65;font-size:1em}.App::-webkit-scrollbar{display:none}.App{-ms-overflow-style:none;scrollbar-width:none}
.Footer{min-height:100vh}.Footer .contact__description{padding:150px 20px;display:flex;flex-direction:column;align-items:flex-end}.Footer .contact__description p{text-align:right;font-size:calc(18px + (30 - 18) * ((100vw - 300px) / (1600 - 300)));font-weight:600;max-width:calc(200px + (500 - 200) * ((100vw - 300px) / (1600 - 300)))}.Footer .contact__description p:last-child{margin-top:50px}.Footer .contact__description a{color:#fff}.Footer .contact__description a:hover{color:#2a2a2a}.Footer .copyright{display:flex;position:absolute;bottom:0px;left:0;right:0;justify-content:center}@media only screen and (min-width: 1000px){.Footer .contact__description{padding:50px 150px}}
.work{padding:0 20px;margin:0 0 100px}.work h1{padding-top:50px}.work .work__category{margin:50px 0;display:flex;justify-content:center;grid-gap:-webkit-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:-webkit-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));grid-gap:-mox-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:-mox-calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));grid-gap:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)));gap:calc(40px + (150 - 40) * ((100vw - 300px) / (1600 - 300)))}.work .work__category p{padding:15px}.work .work__category .activeOption{color:#000 !important;background-color:#e08c65}.work .work__grid{-webkit-column-count:2;column-count:2;grid-column-gap:10px;-webkit-column-gap:10px;column-gap:10px}.work .work__grid .work__item{box-shadow:0 1px 1px 0 rgba(0,0,0,.05),0 1px 10px 0 rgba(0,0,0,.5);display:grid;grid-template-rows:1fr auto;margin-bottom:10px;page-break-inside:avoid;-webkit-column-break-inside:avoid;break-inside:avoid}.work .work__grid .work__item img{width:100%;display:block;grid-row:1/-1;grid-column:1}.work .work__grid .work__item p{background-color:#fff;padding:.2em .5em}.work .work__grid .work__item:hover p{background-color:#e08c65;color:#fff}@media only screen and (min-width: 700px){.work .work__grid{-webkit-column-count:3;column-count:3}}@media only screen and (min-width: 1000px){.work{padding:0 150px;margin-top:100px}}
